export default {
    name: 'Articles',

    data() {
        return {
            articles: [],
            numberOfColumns: 1
        }
    },
    async fetch() {
        const { json: data } = await this.$kirby.find({
            "query": "page('home').children",
            "select": {
                "contenttitle": true,
                "contenttext": true,
            }
        }, this.$nuxt.context.app.i18n.locale)
        this.articles = data
    },
}
